import React from "react";
import TextWithLineBreaks from "./TextWithLineBreaks";

function Resume(props) {
  const {year, position, graduation, university, company, details, location } = props.resumeData;
  return (
    <div className="mi-resume mt-30">
      <div className="mi-resume-summary">
        <h6 className="mi-resume-year">{year}</h6>
      </div>
      <div className="mi-resume-details">
        <h5>{position || graduation}</h5>
        <h6 className="mi-resume-company">{company || university}&nbsp;&nbsp;&nbsp;({location})</h6>
        <p><TextWithLineBreaks text={details} /></p>
      </div>
    </div>
  );
};

export default Resume;
